import * as React from "react";
import { cn } from "@/lib/utils";
import { Button } from "./button";
import { IconType } from "react-icons";
import {
  MdPeople,
  MdSavings,
  MdCalendarToday,
  MdSearch,
  MdAttachMoney,
  MdStorefront,
} from "react-icons/md";

// Preset configurations for common empty states
export const emptyStatePresets = {
  "no-sellers": {
    icon: MdStorefront,
    title: "Be the first seller",
    description: "Set up your seller profile to start offering your time to the community.",
    action: {
      label: "Become a Seller",
    },
  },
  "no-wallet-balance": {
    icon: MdSavings,
    title: "No hours yet",
    description: "Browse members to find experts and purchase hours for 1:1 sessions.",
    action: {
      label: "Browse Members",
    },
  },
  "no-offers": {
    icon: MdCalendarToday,
    title: "No offers yet",
    description: "When you book an offer or someone books with you, it will appear here.",
    action: {
      label: "Browse Members",
    },
  },
  "no-search-results": {
    icon: MdSearch,
    title: "No results found",
    description: "Try adjusting your search or filters to find what you're looking for.",
  },
  "no-earnings": {
    icon: MdAttachMoney,
    title: "No earnings yet",
    description: "Complete sessions with buyers to start earning. Earnings are paid out 48 hours after session completion.",
  },
  "no-members": {
    icon: MdPeople,
    title: "No members yet",
    description: "Be the first to join this community and start offering your expertise.",
  },
} as const;

export type EmptyStatePreset = keyof typeof emptyStatePresets;

const sizes = {
  sm: {
    container: "py-8",
    iconWrapper: "p-2.5",
    icon: "h-8 w-8",
    title: "text-base",
    description: "text-sm",
  },
  default: {
    container: "py-12",
    iconWrapper: "p-3",
    icon: "h-10 w-10",
    title: "text-lg",
    description: "text-sm",
  },
  lg: {
    container: "py-16",
    iconWrapper: "p-4",
    icon: "h-12 w-12",
    title: "text-xl",
    description: "text-base",
  },
};

export interface EmptyStateAction {
  label: string;
  onClick: () => void;
  variant?: "default" | "destructive" | "outline" | "secondary" | "ghost" | "link" | "success";
  icon?: IconType | React.ElementType;
}

export interface EmptyStateProps {
  /** Use a preset configuration for common empty states */
  preset?: EmptyStatePreset;
  /** Custom icon (overrides preset) */
  icon?: IconType | React.ElementType;
  /** Title text (overrides preset) */
  title?: string;
  /** Description text (overrides preset) */
  description?: string;
  /** Primary action button */
  action?: EmptyStateAction;
  /** Secondary action button */
  secondaryAction?: EmptyStateAction;
  className?: string;
  size?: "sm" | "default" | "lg";
  children?: React.ReactNode;
}

export function EmptyState({
  preset,
  icon: iconProp,
  title: titleProp,
  description: descriptionProp,
  action,
  secondaryAction,
  className,
  size = "default",
  children,
}: EmptyStateProps) {
  // Merge preset with props (props override preset)
  const presetConfig = preset ? emptyStatePresets[preset] : null;
  const Icon = iconProp ?? presetConfig?.icon;
  const title = titleProp ?? presetConfig?.title ?? "";
  const description = descriptionProp ?? presetConfig?.description;

  const s = sizes[size];

  return (
    <div
      className={cn(
        "flex flex-col items-center justify-center text-center px-4",
        s.container,
        className
      )}
    >
      {Icon && (
        <div className={cn("mb-4 rounded-full bg-muted", s.iconWrapper)}>
          <Icon className={cn(s.icon, "text-muted-foreground")} />
        </div>
      )}
      {title && (
        <h3 className={cn(s.title, "font-semibold text-foreground mb-2 tracking-tight")}>
          {title}
        </h3>
      )}
      {description && (
        <p className={cn(s.description, "text-muted-foreground max-w-sm mb-4")}>
          {description}
        </p>
      )}
      {children}
      {(action || secondaryAction) && (
        <div className="flex items-center gap-3 mt-2">
          {action && (
            <Button
              onClick={action.onClick}
              variant={action.variant || "default"}
              size={size === "sm" ? "sm" : "default"}
            >
              {action.icon && <action.icon className="h-4 w-4" />}
              {action.label}
            </Button>
          )}
          {secondaryAction && (
            <Button
              onClick={secondaryAction.onClick}
              variant={secondaryAction.variant || "outline"}
              size={size === "sm" ? "sm" : "default"}
            >
              {secondaryAction.icon && <secondaryAction.icon className="h-4 w-4" />}
              {secondaryAction.label}
            </Button>
          )}
        </div>
      )}
    </div>
  );
}

EmptyState.displayName = "EmptyState";
