"use client";

import Link from "next/link";
import { MdCalendarToday, MdSchedule } from "react-icons/md";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { EmptyState } from "@/components/ui/empty-state";
import { SessionCard, type SessionData, type SessionStatus } from "@/components/marketplace";
import { SessionCardListSkeleton } from "@/components/ui/skeletons";

// Use SessionData type from marketplace components
type Session = SessionData;

interface SessionsTabProps {
  experienceId?: string;
  sessions: Session[];
  userId: string;
  isLoading?: boolean;
  standalone?: boolean; // When true, uses /app paths instead of /experiences
}

export function SessionsTab({
  experienceId,
  sessions,
  userId,
  isLoading,
  standalone,
}: SessionsTabProps) {
  // Generate base URLs based on context
  const browseUrl = standalone ? "/app?tab=browse" : `/experiences/${experienceId}?tab=browse`;
  const upcomingSessions = sessions.filter((s) =>
    ["REQUESTED", "ACCEPTED", "IN_PROGRESS"].includes(s.status)
  );
  const pastSessions = sessions.filter(
    (s) =>
      !["REQUESTED", "ACCEPTED", "IN_PROGRESS"].includes(s.status)
  );

  return (
    <div className="p-4 sm:p-6 md:p-8">
      {/* Header */}
      <div className="mb-6">
        <h1 className="text-2xl font-semibold text-foreground">Offers</h1>
        <p className="text-muted-foreground mt-1">
          Manage your upcoming and past offers
        </p>
      </div>

      <Tabs defaultValue="upcoming" className="space-y-4">
        <TabsList>
          <TabsTrigger value="upcoming" className="gap-2">
            Upcoming
            {upcomingSessions.length > 0 && (
              <Badge variant="secondary">{upcomingSessions.length}</Badge>
            )}
          </TabsTrigger>
          <TabsTrigger value="past">Past</TabsTrigger>
        </TabsList>

        <TabsContent value="upcoming" className="space-y-4">
          {isLoading ? (
            <SessionCardListSkeleton count={3} />
          ) : upcomingSessions.length === 0 ? (
            <EmptyState
              preset="no-offers"
            >
              <Link href={browseUrl}>
                <Button>Browse Members</Button>
              </Link>
            </EmptyState>
          ) : (
            <div className="space-y-4">
              {upcomingSessions.map((session) => (
                <SessionCard
                  key={session.id}
                  session={session}
                  experienceId={experienceId}
                  userId={userId}
                  standalone={standalone}
                />
              ))}
            </div>
          )}
        </TabsContent>

        <TabsContent value="past" className="space-y-4">
          {isLoading ? (
            <SessionCardListSkeleton count={3} />
          ) : pastSessions.length === 0 ? (
            <EmptyState
              icon={MdSchedule}
              title="No past sessions"
              description="Your completed sessions will appear here."
            />
          ) : (
            <div className="space-y-4">
              {pastSessions.map((session) => (
                <SessionCard
                  key={session.id}
                  session={session}
                  experienceId={experienceId}
                  userId={userId}
                  standalone={standalone}
                />
              ))}
            </div>
          )}
        </TabsContent>
      </Tabs>
    </div>
  );
}
